.btn-main {
  background-color: #2a2a2a;
  color: #ffffff;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  padding: 10px 30px;
  @extend .animation-transition;

  &:not(.no-hover):hover {
    color: #ffffff !important;
    background-color: $primary;
    text-decoration: none;
  }
}

.btn-main-small {
  @extend .btn-main;
  font-size: 13px;
  line-height: 24px;
  padding: 5px 10px;
  font-weight: 400;
}

.btn-outline {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #2a2a2a;
  color: #2a2a2a;
  font-size: 13px;
  line-height: 22px;
  @extend .animation-transition;

  &:hover {
    background-color: #2a2a2a;
    color: #ffffff;
  }

  &.active {
    background-color: $primary;
    color: $text-hover;
    border-color: $primary;

    @media not all and (hover: none) {
      &:hover {
        color: $text-hover;
        background-color: $text-color;
        border-color: $text-color;
      }
    }
  }
}
